					<div class="flexCenter statusLabel huiStatus" style="margin-bottom: .22rem;" v-else-if="detailData.status == 'stopped'">Cancelled</div>
				</div>
				<div class="cancelReasonBox flexStart">
					<p v-if="detailData.status == 'expired'">This video has expired.<br>Post new videos to earn revenue.</p>
					<p v-if="detailData.status == 'cancelled'">This video has been cancelled by you.</p>
					<p v-if="detailData.status == 'stopped'">This video has been cancelled.<br>Please contact customer service for details</p>
				</div>
                <div class="rentalMsgItem flexBetween">
                    <div class="rentalMsgItemLeft">Total views:</div>
                    <div class="rentalMsgItemRight">{{detailData.totalView}}</div>
                </div>
