<script>
	import https from '../api/https'
	export default {
		name: 'VideoDetails',
		data: function(){
			return {
				bgWrapHeight: 200,
                openStroe: true,
                moneySymbol: https.moneySymbol,
				detailData: null,
                charts: {
                    columns: ['2019','2020','2021','2022','2023','2024','2025','2026'],
                    values: [600,1200,1650,1800,2000,2800,1500,2312]
                },
				cancelAlertBox: false,
			}
		},
		created(){
			this.getData()

			if(this.$route.params.VHinvite_code && this.$route.params.VHinvite_code != 'undefined') {
				localStorage.setItem('VHinvite_code',this.$route.params.VHinvite_code)
				this.VHinvite_code = this.$route.params.VHinvite_code
			}else if(localStorage.getItem('VHinvite_code') && localStorage.getItem('VHinvite_code') != 'undefined') {
				this.VHinvite_code = localStorage.getItem('VHinvite_code')
			}else {
                this.VHinvite_code = ''
				localStorage.setItem('VHinvite_code','')
            }
		},
		mounted: function () {
			let viwePortHeight = document.documentElement.clientHeight || document.body.clientHeight
			let navBarHeight = this.$refs.navBarHeight.offsetHeight
			this.bgWrapHeight = viwePortHeight - navBarHeight
		},
		methods: {
			getData: function(){
				this.showLoading = true
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
				let params = {
					id: this.$route.params.id
				}
				https.fetchGet('/api/video/creation/item',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
						this.detailData = res.data
						if(this.detailData.recently.length > 0) {
							for (let index = 0; index < this.detailData.recently.length; index++) {
								this.charts.columns.push(this.detailData.recently[index].date)
								this.charts.values.push(this.detailData.recently[index].earn)
							}
							setTimeout(() => {
								this.drawChart()
							}, 200);
						}
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
						this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getData()
					}
				})
			},
            drawChart() {
                let myChart = this.$echarts.init(document.getElementById("main"));
                console.log(myChart)
				let option = {
					
					textStyle: {
						color: '#BAC3CC',
					},
					grid: {
						show: true,
						top: '10%',
						// bottom: '10%',
						// right: '4%',
						left: '2.5%',
						width: '95%',
						height: '90%',
						containLabel: true,
						borderColor: '#fff',
						// shadowBlur: 10,
						// shadowColor: '#FF9234'
					},
					xAxis: {
						data: this.charts.columns,
						type:'category',
						boundaryGap: true,
						axisLine: {
                            type: 'dashed',
                            show: false,
							lineStyle : {
								color: '#BAC3CC'
							}
						},
						axisTick: {
							// alignWithLabel: true,
                            show: false,
							interval: 0,
							lineStyle: {
								color: '#BAC3CC'
							}
						},
						axisLabel: {
							// interval: 0,
						},
						splitLine: {
							show: false,
							interval: 0,
							lineStyle: {
								color: '#BAC3CC',
                                width: 0,
							}
						},
                        minorSplitLine: {
                            show: true,
                            color: 'green'
                        }
                       
                        
						
					},
					yAxis:	 {
						type: 'value',
						position: 'left',
						axisLine: {
							show: false,
							lineStyle : {
								color: '#BAC3CC'
							}
						},
						// min: 'dataMin',
						// max: 'dataMax',
						splitNumber: '4',
						// interval: '2',
						axisLabel: {
							show: true,
							interval: 2,
							// showMinLabel: false,
							// showMaxLabel:false,
						},
						axisTick: {
							show: false,
							interval: 2,
							lineStyle: {
								color: '#BAC3CC'
							}
						},
						splitLine: {
							show: true,
							interval: 2,
							lineStyle: {
                                type: 'dashed',
								color: '#BAC3CC',
                                join:'bevel'
							}
						},
						// axisPointer: {
						// 	show: true,
						// 	type: 'shadow'
						// }
						// data: this.productList.charts.values,
						// type:'category',
						// boundaryGap: true,
					},
					series: [
						{
							// name: "num",
							color: '#FF9234',
							type: "line",
							data: this.charts.values
						}
					]
				}
				myChart.setOption(option);
			},
			cancelVideo(){
				this.cancelAlertBox = true
			},
			goCancelVideo: function(){
				this.showLoading = true
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
				let params = {
					id: this.$route.params.id
				}
				https.fetchPost('/api/video/cancel',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
						this.getData()
						this.cancelAlertBox = false	
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
						this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getData()
					}
				})
			},
			goPage(page){
				if(this.VHinvite_code) {
					page = page +'/'+ this.VHinvite_code
				}
				this.$router.push(page)
			},
			goBack(){
				this.$router.go(-1)
			},
		}
	}
</script>
